import React,{Component} from 'react';
import './App.css';
import axios from "axios";
import cookie from "js-cookie";

class PizzaList extends Component{
    constructor(props) {
        super(props);
        this.state = {
            //document.location.host
            access_token:'',
            dataLoaded:false,
            permission:true,
            datalist: []
        };

        // this.props.headtitle('Users  ');
    }

    componentDidMount(){//list list of pizza

        var params = {}
        var headers = {
            'lang':'en',
            'Access-Control-Allow-Origin': "*",
            'Access-Control-Allow-Methods':"*",
            'Access-Control-Allow-Headers': "*"
        }

        axios.post(process.env.REACT_APP_HOST+"pizza", {params, headers} )
            .then(response => {
                 this.setState({
                     dataLoaded: true,
                     datalist: response.data.message
                 });

            }).catch((err) => {
            console.log("AXIOS ERROR: ", err);
        })

    }

    render() {
      return (
          <div>
              {
                  this.state.datalist.map(
                      row => {
                          return (
                  <div className="col-md-3 text-center  " key={row.id}>
                  <div className="item">
                  <img src={"assets/images/"+row.image} />
                  <br/>
                      {row.name} - <strong>${row.price}</strong>
                  <br/>
                  <button className="btn btn-danger my-cart-btn" data-id={row.id} data-name={row.name}
                          data-summary={row.name} data-price={row.price}
                  data-quantity="1" data-image={"assets/images/"+row.image} >Add to Cart
                  </button>

                  </div>
                  </div>
                          )
                      })
              }
          </div>
      );
  }
}

export default PizzaList;
