import React, { Component } from "react";
import ReactDOM from "react-dom";
import { Link, Route, BrowserRouter as Router, Switch } from "react-router-dom";
import PizzaList from "./PizzaList";
import Checkout from "./Checkout";
import Register from "./Register";
import Login from "./Login";
import Orders from "./Orders";
import Header from "./Header";

export default class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
           loaded:0,
           head:''
        }
        this.updateState=this.updateState.bind(this);
        this.updateTitle=this.updateTitle.bind(this);

    }
    updateTitle(_title){

        this.setState((prevState) => {
            return {
                head:_title
            }
          })
       
        return this.state.head;
    }
    updateState(status){
       if(status==null){
        return this.state.loaded;
       }else{
            this.setState({
                loaded:status
            });            
        }
        console.log( (status+"is send status  saved status ="+this.state.loaded));
        return this.state.loaded;
    } 
    componentDidMount(){
        this.setState({
            loaded:true
        });
    }
    render() {
        return (
            <div>
                <Router >
               <Header/>
                <div  id="content" style={{padding: 60,clear: 'both'}}>
                    <div className="page-wrapper">
                        <div className="container-fluid">
                            <div id="content">
                                <Switch>
                                <Route
                                    exact
                                    path={'/'}
                                    component={PizzaList}
                                />
                                 <Route
                                    exact
                                    path="/checkout"
                                    component={() => <Checkout
                                        headtitle={this.updateTitle}  
                                        status={this.updateState} 
                                         />} 
                                />

                                    <Route exact
                                        path="/register"
                                        component={Register}
                                    />

                                    <Route exact
                                        path="/login"
                                        component={Login}
                                    />
                                    <Route exact
                                        path="/orders"
                                        component={Orders}
                                    />

                                    <Route exact path="/orders"
                                 component={() => <Checkout
                                    headtitle={this.updateTitle}  
                                    status={this.updateState} 
                                     />} 
                                />
                                <Route
                                    exact
                                    path="/order/edit/:id"
                                    render={props => 
                                    <Checkout
                                    headtitle={this.updateTitle}  
                                    status={this.updateState} 
                                      {...props}/>}
                                />

                                <Route
                                    exact
                                    path="*"
                                    render={props =>{return "Error 404 Page Not Found !"} }
                                />
                                </Switch>
                            </div>
                        </div>
                      
                    </div>
                </div>
                </Router>
            </div>
        );
    }
}
if (document.getElementById("root")) {
    ReactDOM.render(<App />, document.getElementById("root"));
}