import React,{Component} from 'react';
import './App.css';
import axios from "axios";
import Error from "./Error";
import cookie from "js-cookie";
import Message from "./Message";
class Register extends Component
{
    constructor(props) {
        super(props);
        this.state = {
            access_token: '',
            dataLoaded: false,
            errors:'',
            name:'',
            mobile:'',
            email:'',
            password:'',
            address:''
        };

        // this.props.headtitle('Users  ');
    }
    handleInputChange = e => {
        this.setState({
            [e.target.name]: e.target.value
        });
        // alert([e.target.name]+e.target.value);
    };
    handleForm= e => {
        e.preventDefault();
        var headers = {
            'lang': 'en',
            'Access-Control-Allow-Origin': "*",
            'Access-Control-Allow-Methods': "*",
            'Access-Control-Allow-Headers': "*"
        }
        var params=JSON.stringify(this.state);
        axios.post(process.env.REACT_APP_HOST + "user/create", {params, headers})
            .then(response => {
                if(response.data.statusCode==200){
                  //  alert(response.data.message);
                    cookie.set("token", response.data.access_token);
                    localStorage.products=[];
                    window.location.href=window.location.protocol + "//" +window.location.host+'/orders';
                    this.setState({
                        message:response.data.message
                    })
                }else{
                   this.setState({
                       errors:response.data.errors,
                       message:response.data.message,
                   })
                }

            }).catch((err) => {
            console.log("AXIOS ERROR: ", err);
        })


    }
    render()
    {

        return (
            <div>
                <Message
                    msg={
                        this.state.message
                            ? this.state.message
                            : 'Register'
                    }
                />

                <form method="post" className="col-sm-8" onSubmit={this.handleForm}>
                    <div className="form-group row">
                        <label className="col-sm-2 col-form-label">Name</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name="name" placeholder="Name"  onChange={this.handleInputChange}/>
                            <Error
                                error={
                                    this.state.errors["name"]
                                        ? this.state.errors["name"]
                                        : null
                                }
                            />
                        </div>
                    </div>

                    <div className="form-group row">
                        <label className="col-sm-2 col-form-label">Mobile</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name="mobile" placeholder="Mobile" onChange={this.handleInputChange}/>
                            <Error
                                error={
                                    this.state.errors["mobile"]
                                        ? this.state.errors["mobile"]
                                        : null
                                }
                            />
                        </div>
                    </div>

                    <div className="form-group row">
                        <label className="col-sm-2 col-form-label">Email</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name="email" placeholder="Email" onChange={this.handleInputChange}/>
                            <Error
                                error={
                                    this.state.errors["email"]
                                        ? this.state.errors["email"]
                                        : null
                                }
                            />
                        </div>
                    </div>


                    <div className="form-group row">
                        <label className="col-sm-2 col-form-label">Password</label>
                        <div className="col-sm-10">
                            <input type="password" className="form-control" name="password" placeholder="Paassword" onChange={this.handleInputChange}/>
                            <Error
                                error={
                                    this.state.errors["password"]
                                        ? this.state.errors["password"]
                                        : null
                                }
                            />
                        </div>
                    </div>

                    <div className="form-group row">
                        <label className="col-sm-2 col-form-label">Address</label>
                        <div className="col-sm-10">
                            <textarea className="form-control" name="address" placeholder="Address"  onChange={this.handleInputChange}></textarea>
                            <Error
                                error={
                                    this.state.errors["address"]
                                        ? this.state.errors["address"]
                                        : null
                                }
                            />
                        </div>
                    </div>

                    <div className="form-group text-center">
                        <button type="submit" className="btn-primary btn" >
                          Create Account
                        </button>
                    </div>

                </form>

            </div>
        );
    }

}
export default Register;
