import React,{Component} from 'react';
import './App.css';
import axios from "axios";
import cookie from "js-cookie";
import Message from "./Message";
import Error from "./Error";


class Checkout extends Component {
    constructor(props) {
        super(props);
        if(cookie.get("info")!=undefined){
            var info=JSON.parse(cookie.get("info"));
            this.state = {
                access_token: '',
                datalist:[],
                dataLoaded: false,
                name:info.name,
                mobile:info.mobile,
                email:info.email,
                address:info.address,
                errors:[]
            };
        }else{
            this.state = {
                access_token: '',
                datalist:[],
                dataLoaded: false,
                name:'',
                mobile:'',
                email:'',
                address:'',
                errors:[]
            };
        }

    }
    handleInputChange = e => {
        this.setState({
            [e.target.name]: e.target.value
        });
       // alert([e.target.name]+e.target.value);
    };
    submitOrder(){

        var headers = {
            'lang': 'en',
            'Access-Control-Allow-Origin': "*",
            'Access-Control-Allow-Methods': "*",
            'Access-Control-Allow-Headers': "*"
        }
        var params=JSON.stringify(this.state);
        axios.post(process.env.REACT_APP_HOST + "order/create", {params, headers})
            .then(response => {
                if(response.data.statusCode==200){
                    alert(response.data.message);
                    localStorage.products=[];
                    window.location.href=window.location.protocol + "//" +window.location.host;
                }

                if(response.data.statusCode==200){

                    this.setState({
                        message:response.data.message
                    })
                }else{
                    this.setState({
                        errors:response.data.errors,
                        message:response.data.message
                    })
                }



            }).catch((err) => {
            console.log("AXIOS ERROR: ", err);
        })


    }
    componentDidMount() {//list list of pizza

        var params = localStorage.products;
        var headers = {
            'lang': 'en',
            'Access-Control-Allow-Origin': "*",
            'Access-Control-Allow-Methods': "*",
            'Access-Control-Allow-Headers': "*"
        }

        axios.post(process.env.REACT_APP_HOST + "productsDetails", {params, headers})
            .then(response => {

                this.setState({
                    dataLoaded: true,
                    datalist: response.data.message,
                    //name:cookie.get("info").name,
                    total_price: response.data.total_price,
                    deliveryFees: response.data.deliveryFees,
                    total_quantity:response.data.total_quantity
                });

            }).catch((err) => {
            console.log("AXIOS ERROR: ", err);
        })

    }

    render() {
        if (this.state.datalist.length < 1) {
            return (<div>Loading Data ... !</div>);
        }
        return (
            <div>
                <div>
                    <Message
                    msg={
                        this.state.message
                            ? this.state.message
                            : 'Checkout'
                    }
                />
                </div>

                <div className="col-sm-6" style={{backgroundColor: '#eeeeee', padding: 20}}>
                    <form method="post">
                        <div className="form-group row">
                            <label className="col-sm-2 col-form-label">Name</label>
                            <div className="col-sm-10">
                                <input type="text" value={this.state.name} className="form-control" name="name" placeholder="Name"  onChange={this.handleInputChange}/>
                                <Error
                                    error={
                                        this.state.errors["name"]
                                            ? this.state.errors["name"]
                                            : null
                                    }
                                />
                            </div>
                        </div>

                        <div className="form-group row">
                            <label className="col-sm-2 col-form-label">Mobile</label>
                            <div className="col-sm-10">
                                <input type="text" value={this.state.mobile}  className="form-control" name="mobile" placeholder="Mobile" onChange={this.handleInputChange}/>
                                <Error
                                    error={
                                        this.state.errors["mobile"]
                                            ? this.state.errors["mobile"]
                                            : null
                                    }
                                />
                            </div>
                        </div>

                        <div className="form-group row">
                            <label className="col-sm-2 col-form-label">Email</label>
                            <div className="col-sm-10">
                                <input type="text" value={this.state.email}  className="form-control" name="email" placeholder="Email" onChange={this.handleInputChange}/>
                                <Error
                                    error={
                                        this.state.errors["email"]
                                            ? this.state.errors["email"]
                                            : null
                                    }
                                />
                            </div>
                        </div>

                        <div className="form-group row">
                            <label className="col-sm-2 col-form-label">Address</label>
                            <div className="col-sm-10">
                                <textarea value={this.state.address} className="form-control" name="address" placeholder="Address"  onChange={this.handleInputChange}></textarea>
                                <Error
                                    error={
                                        this.state.errors["address"]
                                            ? this.state.errors["address"]
                                            : null
                                    }
                                />
                            </div>
                        </div>

                        <div className="form-group text-center">
                            <button type="button" className="btn-primary btn" id="submit_order" onClick={this.submitOrder.bind(this)}>
                                Submit Your Order
                            </button>
                        </div>

                    </form>

                </div>
                <div className="col-sm-6">
                    <table className="table table-hover table-responsive products text-center">
                        <tbody>
                        <tr>
                            <td>#</td>
                            <td>Pizza Name</td>
                            <td>Unit Price</td>
                            <td>Quantity</td>
                            <td>Total Price</td>
                        </tr>
                        {
                            this.state.datalist.map(
                                row => {
                                    return (
                                        <tr key={row.id}>
                                            <td className="text-center" style={{width: 30}}>
                                                <img src={"assets/images/" + row.image}
                                                     style={{width: 30, height: 30}}/></td>
                                            <td>{row.name}</td>
                                            <td title="Unit Price" className="text-right">{row.price}</td>
                                            <td title="Quantity">
                                                {row.quantity}
                                            </td>
                                            <td title="Total" className="text-right my-product-total">
                                                ${row.total_price}</td>
                                            <td title="Remove from Cart" className="text-center" style={{width: 30}}>
                                            </td>
                                        </tr>

                                    )
                                })
                        }

                        <tr style={{color:'green'}}>
                            <td></td>
                            <td colSpan={2}>
                                <h4> Price : ${this.state.total_price}  </h4>
                                <h4> Quantity : {this.state.total_quantity} Pizza </h4>

                            </td>
                            <td colSpan={2}>
                                <h4> Delivery Fees : ${this.state.deliveryFees}  </h4>
                                <h4> Total Price : ${this.state.total_price+this.state.deliveryFees}  </h4>
                            </td>
                        </tr>
                        </tbody>
                    </table>



                </div>
            </div>
        );
    }
}

export default Checkout;
