import React from "react";
import { Link, Route, BrowserRouter as Router, Switch } from "react-router-dom";
import cookie from "js-cookie";

export default function Header() {
if(cookie.get("info")!=undefined){
    var info=JSON.parse(cookie.get("info"));
    return (
        <div  id="header" className="head col-sm-12"
              style={{backgroundColor: '#000000',color: '#ffffff',paddingBottom: 10}}>
          <div className="col-sm-4">
            <h4 style={{marginTop:15}}>
              <Link to="/" className="btn btn-primary" >Home</Link>  &nbsp;&nbsp;
              <Link to="/orders" className="btn btn-info" type="button">My Orders</Link>
            </h4>
          </div>
          <div className="col-sm-4"><h1>THE YUMMI PIZZA</h1></div>
          <div  className="col-sm-4" style={{cursor: 'pointer',textAlign: "right"}}>
            <h3><span className="glyphicon glyphicon-shopping-cart my-cart-icon">
                            <span className="badge badge-notify my-cart-badge"></span></span>
            <span>{info.name}</span>
            </h3>
          </div>
        </div>
    )
}else{
    return (
        <div  id="header" className="head col-sm-12"
              style={{backgroundColor: '#000000',color: '#ffffff',paddingBottom: 10}}>
          <div className="col-sm-4">
            <h4 style={{marginTop:15}}>
              <Link to="/" className="btn btn-primary" >Home</Link>  &nbsp;&nbsp;
              <Link to="/login" className="btn btn-danger" >Login</Link>  &nbsp;&nbsp;
              <Link to="/register" className="btn btn-info" type="button">Register</Link>
            </h4>
          </div>
          <div className="col-sm-4"><h1>THE YUMMI PIZZA</h1></div>
          <div  className="col-sm-4" style={{cursor: 'pointer',textAlign: "right"}}>
            <h1><span className="glyphicon glyphicon-shopping-cart my-cart-icon">
                            <span className="badge badge-notify my-cart-badge"></span></span></h1>
          </div>
        </div>
    )
}

}
