import React,{Component} from 'react';
import './App.css';
import axios from "axios";
import cookie from "js-cookie";


class Orders extends Component {
    constructor(props) {
        super(props);
        this.state = {
            access_token: '',
            datalist:[],
            dataLoaded: false,
                name:'',
                mobile:'',
                email:'',
                address:''
        };


        // this.props.headtitle('Users  ');
    }

    componentDidMount() {//list list of pizza

        var params = localStorage.products;
        var headers = {
            'lang': 'en',
            'Authorization':'Bearer '+cookie.get("token"),
            'Accept':'application/json',
            'Access-Control-Allow-Origin': "*",
            'Access-Control-Allow-Methods': "*",
            'Access-Control-Allow-Headers': "*"
        }

       // alert('Bearer '+cookie.get("token"));
        axios.post(process.env.REACT_APP_HOST + "orders",params, {
            headers: headers
        })
            .then(response => {

                cookie.set("info",response.data.user_info);

                this.setState({
                    dataLoaded: true,
                    datalist: response.data.message,
                });

            }).catch((err) => {
            console.log("AXIOS ERROR: ", err);
        })

    }

    render() {
        if (this.state.datalist.length < 1) {
            return (<div>Loading previous Orders  ... !</div>);
        }
        return (
            <div>
                <div className="col-sm-12">
                    <table className="table table-hover table-responsive products text-center">
                        <tbody>
                        <tr>
                            <td style={{width:100}}>ID</td>
                            <td style={{width:150}}>Price</td>
                            <td style={{width:150}}>Delivery Fees</td>
                            <td style={{width:150}}>Total Price</td>
                            <td >Products</td>
                        </tr>
                        {
                            this.state.datalist.map(
                                row => {
                                    return (
                                        <tr key={row.id}>
                                            <td>{row.id}</td>
                                            <td>{row.price}</td>
                                            <td>{row.delivery_fees}</td>
                                            <td>{row.total_price}</td>
                                            <td>
                                                {row.products.map(r=>{
                                                    return <h4>{r.name}   = ${r.price} </h4>
                                                      }
                                                    )
                                                }
                                            </td>

                                        </tr>

                                    )
                                })
                        }
                        </tbody>
                    </table>



                </div>
            </div>
        );
    }
}

export default Orders;
